import MenuIcon from '@mui/icons-material/Menu'
import AppBar from '@mui/material/AppBar'
import Button from '@mui/material/Button'
import Container from '@mui/material/Container'
import IconButton from '@mui/material/IconButton'
import Toolbar from '@mui/material/Toolbar'
import Typography from '@mui/material/Typography'
import React, { memo } from 'react'
import { useSelector } from 'react-redux'
import { TaskType } from '../api/todolists-api'
import './App.css'
import { LinearProgress } from '@mui/material'
import { CustomizedSnackbars } from '../components/ErrorSnackBar/ErrorSnackBar'
import { TodolistLIst } from '../features/TodolistsList/TodolistLIst'
import type { AppRootState } from './store'
import type { RequestStatusType } from './app-reducer'
import { BrowserRouter, HashRouter, Route, Routes } from 'react-router-dom'
import { Login } from '../features/Login/Login'

export type TaskStateType = {
  [key: string]: Array<TaskType>
}
type PropsType = {
  demo?: boolean
}
export const App = memo(({ demo = false, ...props }: PropsType) => {
  const status = useSelector<AppRootState, RequestStatusType>(state => state.app.status)
  return (
    <HashRouter>
      <div className="App">
        <CustomizedSnackbars />
        <AppBar position="static">
          <Toolbar>
            <IconButton size="large" edge="start" color="inherit" aria-label="menu" sx={{ mr: 2 }}>
              <MenuIcon />
            </IconButton>
            <Typography variant="h6" component="div" sx={{ flexGrow: 1 }}>
              Menu
            </Typography>
            <Button color="inherit">Login</Button>
          </Toolbar>
        </AppBar>
        {status === 'loading' && (
          <LinearProgress style={{ position: 'absolute', top: '0', left: '0', width: '100%' }} color="primary" />
        )}
        <Container fixed>
          <Routes>
            <Route path={'/'} element={<TodolistLIst demo={demo} />} />
            <Route path={'/Todolist_Youtube/login'} element={<Login />} />
          </Routes>
        </Container>
      </div>
    </HashRouter>
  )
})

export default App
